@import "../../common/styles/sass/variables";

.skill_block {
  align-items: center;
  background-color: #181715;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin: 20px;
  padding: 30px;
  position: relative;
  text-align: center;
  transition: all .5s;
  user-select: none;
  width: 350px;

  .Skill_photo {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: contain;
    height: 60px;
    width: 60px;
  }

  .Skill_title {
    font-size: 27px;
    font-weight: 600;
    line-height: 1.3;
    margin: 10px 0 0;
    padding: 0;
    text-align: center;
    color: $primaryColor;
  }

  .Skill_spanCont {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: 100%;
    inset: 0 0 0 0;
    position: absolute;
    width: 100%;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    -webkit-transition: opacity 0.3s ease-in-out;
    -moz-transition: opacity 0.3s ease-in-out;

    .Skill_description {
      font-size: 20px;
      height: content-box;
      padding: 10px;
      width: 100%;
      color: rgba(245, 245, 245, 0.75);
    }
  }

  &:hover .Skill_spanCont {
    background: #000000;
    opacity: 0.9;
    transition: opacity 0.3s ease-in-out;
    -webkit-transition: opacity 0.3s ease-in-out;
    -moz-transition: opacity 0.3s ease-in-out;
  }
}

